 .home-container{
     display: flex;
     flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
    margin-top: 1vh;
}

.home-title{
    align-self: center;
    font-size: 4.5vh;
    color: floralwhite;
    font-family: 'Questrial', sans-serif;
    text-transform: uppercase;
    margin-bottom: 2vh;
}

.graph-container{
    display: flex;
    justify-content: center;
    align-content: center;
    background-color: rgb(225, 220, 211);
    width: 80vw;
    border-radius: 7.5px;
    padding-bottom: 1vh;
}

